.updatenewsadmin-main{
    background-color: rgb(232, 243, 223);
    padding: 20px;
    border-radius: 10px;
}

.updatenewsadmin-base{
    background-color: rgb(246, 249, 242);
    height: 484px;
}

.updatenewsadmin-input{
    display: block;
    width: 80%;
    font-size: 25px;
    padding: 5px 5px 5px 15px;
    margin: auto;
    margin-top: 5px;
    border: 3px solid rgb(182, 244, 131);
    border-radius: 5px;
}
textarea{
    resize: none;
}

.updatenewsadmin-button{
    background-color: rgb(182, 244, 131);
    border: 0;
    padding: 7px 20px;
    font-size: 25px;
    margin-top: 10px;
    border-radius: 10px;
}
